











































































































































@use "~main/theme"

.printshots-container
  padding: 1rem

.prev-btn
  display: inline
  position: absolute
  left: 2%
  top: 40%
  opacity: 0.5

.next-btn
  display: inline
  position: absolute
  right: 2%
  top: 40%
  opacity: 0.5

.page-visiting
  color: theme.$primary

.page-unvisited
  color: darken(theme.$secondary, 20)
